/* eslint-disable */
<template>
  <div class="bmi">
    <div class="form">
      <p>{{ $t("info.todo") }}</p>
    </div>
    <div class="button">
      <div>
        <button class="myButton" type="submit" @click="calculbmi">{{ $t("profile.calculate") }}</button>
      </div>
      <div class="bmi2">
        <p>{{ $t("info.bmi") }} = {{bmi2}}</p>
      </div>
    </div>
    <div class="infocontainer">
      <table class="info">
        <tr>
          <td>{{ $t("info.Underweight") }}</td>
          <td>{{ $t("info.small") }} 18.5</td>
        </tr>
        <tr>
          <td>{{ $t("info.Normalweight") }}</td>
          <td>18.5 - 24.9</td>
        </tr>
        <tr>
          <td>{{ $t("info.Overweight") }}</td>
          <td>25 - 29.9</td>
        </tr>
        <tr>
          <td>{{ $t("info.Obesity") }}</td>
          <td>{{ $t("info.great") }} 29.9</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>

export default {
    data() {
      return{
        bmi:'--',
        bmi2:'--',
        size:localStorage.getItem('size') || 0,
        weight:localStorage.getItem('weight') || 0
      };
    },
    methods: {
      calculbmi() {
        if(this.size==0 || this.weight==0){
          this.showAlert();
        }else{
          this.bmi = this.weight/(this.size*this.size)
          this.bmi2=parseFloat(this.bmi).toFixed(1)
        }    
      },
      showAlert(){
        var ok ="Ok";
        var ok2="Окей";
        var message3="no values were found, please check your profile."
        var message2="Значений не найдено, пожалуйста, посетите свой профиль.";
        var message="Es wurden keine Werte gefunden, bitte schauen Sie sich Ihr Profil an.";
        var lang = localStorage.getItem('lang') || 'en';

        if(lang =="en"){
            const options = { size: 'sm', okLabel:ok}
            this.$dialogs.alert(message3, options)
            .then(res => {
                console.log(res) // {ok: true|false|undefined}
            })
        }else if(lang =="de"){
            const options = { size: 'sm', okLabel:ok}
            this.$dialogs.alert(message, options)
            .then(res => {
                console.log(res) // {ok: true|false|undefined}
            })

        }else{
            const options = { size: 'sm', okLabel:ok2}
            this.$dialogs.alert(message2, options)
            .then(res => {
                console.log(res) // {ok: true|false|undefined}
            })

        }
      }
    }
};
</script>

<style scoped>
  .bmi{ 
    text-align: center;
  }

  table{
    margin:auto;
    width:30%;
  }

  .info td, .info th {
    border: 1px solid #ddd;
    padding: 8px;
  }

  .info tr:nth-child(even){
    background-color: #f2f2f2;
  }

  .form{
    margin: auto;
  }

  .form p{
    font-size:25px;
  }

  .myButton {
	    background-color:#41AA64;
	    border-radius:28px;
	    border:1px solid #18ab29;
	    display:inline-block;
	    cursor:pointer;
	    color:#ffffff;
	    font-family:Arial;
      font-weight: bold;
	    font-size:17px;
	    padding:16px 31px;
    
	    text-decoration:none;
	    text-shadow:0px 1px 0px #2f6627;
    }
    .myButton:hover {
	    background-color:#247C2F;
        transition:0.5s;
    }
    .myButton:active {
	    position:relative;
	    top:1px;
    }

    .bmi2{
      font-size:40px;
    }
</style>